<template>
  <div class="w-full flex flex-col h-screen overflow-auto">

    <Head title="成绩"></Head>
    <main v-if="score.length === 0" class="flex-1 flex flex-col justify-center items-center">
      <Image :src="src" />
      <span class="text-[12px] font-light">近一个月暂无成绩通知</span>
    </main>
    <main v-else>
      <van-cell-group>
        <van-cell v-for="value in score" :title="value.type" :value="value.score" :key="value.type"></van-cell>
      </van-cell-group>
    </main>
  </div>

</template>
<script setup lang='ts'>
import Head from '@/components/head.vue';
import Image from '@/components/image.vue';
import { score as Score } from '@/request/score/index.ts';
import { ref } from 'vue';
const src = ref<string>('/src/assets/image/note.png')
// const show = ref<boolean>(true)
interface scoreType {
  type: string;
  score: number
}
const score = ref<Array<scoreType>>([])
onMounted(() => {
  Score('/api/score?type=1', 'get').then(res => {
    score.value = res.data.data
  })
})
</script>
